import React from 'react';
import './App.css';
import header from './header.JPG';
import logo from './Peteng2.png';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import TextField from '@material-ui/core/TextField';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Snackbar from '@material-ui/core/Snackbar';
import CloseIcon from '@material-ui/icons/Close';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import {BrowserRouter, Route, Link, Switch} from 'react-router-dom';


class Jumboo extends React.Component{

	render(){

		return(
			<div className="animated fadeIn">
				<img src={logo} className="App-profil" alt="logo" />
				<a className="App-nama"> Alvinanda R.K. </a>
				<DisabledTabs  />
        <main>
        <Switch>
        <Route path='/postAkun' exact component={PostAkun} />
         <Route path='/biodata' exact component={Biodata} />
         <Route path='/penelitian' exact component={Penelitian} />
         <Route path='/pengabdian' exact component={Pengabdian} />
         <Route path='/' component={Penelitian} />
        </Switch>
        </main>

			</div>

		)
	}
}

export default Jumboo;

function DisabledTabs() {
  const [value, setValue] = React.useState(2);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Paper square>
      <Tabs
      value={value}
      indicatorColor="primary"
      textColor="primary"
      onChange={handleChange}
      aria-label="disabled tabs example"
      >
    			<Link to='/postAkun'>	<Tab label="Post" /> </Link>
          <Link to='/biodata'> <Tab label="Profil" /> </Link>
          <Link to='/penelitian'>  <Tab label="Peneletian" /> </Link>
          <Link to='/pengabdian'> <Tab label="Pengabdian" /> </Link>
      </Tabs>
    </Paper>
  );
}

function PostAkun(){
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [open, setOpen] = React.useState(false);


  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClosed = () => {
    setOpen(false);
  };

  return (
    <div>
      <Penelitian />
      <Button> <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a></Button>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
      <Button className="f" aria-controls="simple-menu" aria-haspopup="true" color="primary" onClick={handleClick}>
        Opsi
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
      <Link to="/editPosting">
       <MenuItem onClick={handleClose}>Edit</MenuItem>
       </Link>

      </Menu>
      <br/>
      <br/>
      <Pengabdian />
      <Button> <a className="App-comment"> <ChatBubbleOutlineIcon /> Comment </a></Button>
      <Button> <a className="App-comment"> <ThumbUpAltOutlinedIcon /> Like </a></Button>
      <Button className="f" aria-controls="simple-menu" aria-haspopup="true" color="primary" onClick={handleClick}>
        Opsi
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Link to='/editPosting'>
        <MenuItem onClick={handleClose}>Edit</MenuItem>
        </Link>
        <MenuItem onClick={handleClickOpen}>Hapus</MenuItem>
        <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Use Google's location service?"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Apakah anda yakin ingin menghapus postingan?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleClosed} color="primary">
            Tidak
          </Button>
          <Button onClick={handleClosed} color="primary" autoFocus>
            Ya
          </Button>
        </DialogActions>
      </Dialog>
      </Menu>
    </div>
  );
}

