import React, {Component} from 'react';
import './App.css';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import {BrowserRouter, Route, Link, Switch} from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import {
  Badge,
  ButtonGroup,
  ButtonToolbar,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
	CardTitle,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';

class Jurnal extends React.Component{
  render(){
    return(
      <BrowserRouter>
        <div className="animated fadeIn" >
          <Header />
          <DisabledTabs />
          <main>
            <Switch>
              <Route path='/penelitian' exact component={Penelitian} />
              <Route path='/pengabdian' exact component={Pengabdian} />
              <Route path='/' component={Penelitian} />
            </Switch>
          </main>
          </div>
          </BrowserRouter>
    );
  }
}

export default Jurnal;

function Header(){
  return(
    <div className="App judul">
      <h1> Research </h1>
      <p> Find research to grow your world.</p>
    </div>
  );
}

function DisabledTabs() {
  const [value, setValue] = React.useState(1);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Paper square>
      <Tabs

        indicatorColor="primary"
        textColor="primary"
        onChange={handleChange}
        aria-label="disabled tabs example"
      >
      <Link to='/penelitian'>  <Tab label="Penelitian" /> </Link>
      <Link to='/pengabdian'>  <Tab label="Pengabdian" /> </Link>
      </Tabs>
    </Paper>
  );
}

function Penelitian(){
	return(
		<Row>
			<Col>
				<Card>
					<CardBody>
						<Row>
							<Col sm="5">
								<CardTitle className="mb-0">Peneletian</CardTitle>
							</Col>
						</Row>
					</CardBody>
					<CardFooter>
					<a className="App-judul">Pengembangan Sistem Repository dan Sosial Media untuk Dosen Berbasis Website  </a>
					<br/>
					<Button href="#text-buttons" color="primary">
  				<Link to='/post'>See Full Document</Link>
					</Button>
					<a> Januari, 2020 </a>
					<br/>
					<br/>
					<Button variant="contained" color="primary">
					  Download
					</Button>
					</CardFooter>
				</Card>
			</Col>
		</Row>
	);
}

function Pengabdian(){
	return(
		<Row>
			<Col>
				<Card>
					<CardBody>
						<Row>
							<Col sm="5">
								<CardTitle className="mb-0">Pengabdian</CardTitle>
							</Col>
						</Row>
					</CardBody>
					<CardFooter>
					<a className="App-judul">Rancang Bangun Sistem Informasi Manajemen Kelurahan (Studi Kasus di Kelurahan Kauman dan Kasin Kota Malang)</a>
					<br/>
					<a> Januari, 2020 </a>
					</CardFooter>
				</Card>
			</Col>
		</Row>

	);
}
